<template>
    <div>
        <collapse title="入门01" open>
            <div id="kkk" style="width:600px;height:400px;"></div>
        </collapse>

        <collapse title="" open>
        </collapse>

    </div>
</template>

<script>
export default {
    data(){
        return {}
    },
    mounted(){       
        // 基于准备好的dom，初始化echarts实例
        var myChart = this.$echarts.init(document.getElementById('kkk'));

        // 指定图表的配置项和数据
        var option = {
            title: {
                text: 'ECharts 入门示例'
            },
            tooltip: {},
            legend: {
                data:['销量']
            },
            xAxis: {
                data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'bar',
                data: [5, 20, 36, 10, 10, 20]
            }]
        };

        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
    },
}
</script>

<style lang="scss">

</style>


